import TripPointView from '../view/point-view';
import TripPointEditView from '../view/edit-point-view';
import { render, replace, remove } from '../framework/render';
import { StatusForm, UserAction, UpdateType } from '../const';

/**
 * @class     :  +  .
 */
export default class PointPresenter {
  #pointListContainer = null; //     
  #point = null; //   
  #destinations = null; //   
  #offers = null; //   
  #pointComponent = null;
  #pointEditComponent = null;
  #handleCloseAllForm = null;
  #handleDataUpdate = null;
  #mode = StatusForm.DEFAULT;

  /**
   * @constructor
   * @param {Object} params
   * @param {HTMLElement} params.pointListContainer  `<ul>`   
   * @param {Object} params.point   
   * @param {Array} params.destinations  
   * @param {Array} params.offers  
   * @param {function} params.handleEditTypeChange - -   
   * @param {function} params.handleDataChange - -,     
   */
  constructor({ pointListContainer, point, destinations, offers, handleEditTypeChange, handleDataChange }) {
    this.#pointListContainer = pointListContainer;
    this.#point = point;
    this.#destinations = destinations;
    this.#offers = offers;
    this.#handleCloseAllForm = handleEditTypeChange;
    this.#handleDataUpdate = handleDataChange;
  }

  /**
   *   PointPresenter
   */
  init() {
    this.#pointComponent = new TripPointView({
      point: this.#point,
      destinations: this.#destinations,
      offers: this.#offers,
      onEditFormButtonClick: this.#handleOpenFormArrow,
      onFavoriteButtonClick: this.#handleFavoriteButton,
    });

    this.#pointEditComponent = new TripPointEditView({
      point: this.#point,
      destinations: this.#destinations,
      offers: this.#offers,
      onCloseEditFormButtonClick: this.#handleCloseFormArrow,
      onSaveFormButtonClick: this.#handleSaveButton,
      onDeletePointButtonClick: this.#handleDeleteButton,
    });

    render(this.#pointComponent, this.#pointListContainer);
  }

  /**
   * @description     
   */
  destroy() {
    remove(this.#pointComponent);
    remove(this.#pointEditComponent);
  }

  /**
   * @description      
   */
  resetViewToDefault() {
    if (this.#mode !== StatusForm.DEFAULT) {
      this.#replaceFormToCard();
    }
  }

  //  ,    replace 
  #handleOpenFormArrow = () => {
    this.#replaceCardToForm();
  };

  //  ,    replace 
  #handleCloseFormArrow = () => {
    this.#replaceFormToCard();
  };

  /**
  * @description      ""
  */
  #handleFavoriteButton = () => {
    this.#point.isFavorite = !this.#point.isFavorite;
    this.#handleDataUpdate(
      UserAction.UPDATE_POINT,
      UpdateType.MINOR,
      this.#point
    );
  };

  /**
   * @description    
   */
  #replaceCardToForm = () => {
    replace(this.#pointEditComponent, this.#pointComponent); //     (  .5.3)
    document.addEventListener('keydown', this.#escKeyDownHandler);
    this.#handleCloseAllForm(); //    MAP    resetViewToDefault() -     
    this.#mode = StatusForm.EDIT; //    - " "
  };

  /**
   * @description    
   */
  #replaceFormToCard = () => {
    this.#pointEditComponent.reset(this.#point);
    replace(this.#pointComponent, this.#pointEditComponent);
    document.removeEventListener('keydown', this.#escKeyDownHandler);
    this.#mode = StatusForm.DEFAULT;
  };

  /**
   * @description     escape    
   */
  #escKeyDownHandler = (evt) => {
    if (evt.key === 'Escape') {
      evt.preventDefault();
      this.#replaceFormToCard();
      document.removeEventListener('keydown', this.#escKeyDownHandler);
    }
  };

  /**
   * @description   
   */
  _replaceComponent(point) {
    const newPointComponent = new TripPointView({
      point: point,
      destinations: this.#destinations,
      offers: this.#offers,
      onEditFormButtonClick: this.#handleOpenFormArrow,
      onFavoriteButtonClick: this.#handleFavoriteButton,
    });

    replace(newPointComponent, this.#pointComponent);
    this.#pointComponent = newPointComponent;
  }

  /**
   * @description      SAVE
   */
  #handleSaveButton = (updatePoint) => {
    this.#point = updatePoint;
    this.#replaceFormToCard();
    this.#handleDataUpdate(
      UserAction.UPDATE_POINT,
      UpdateType.MINOR,
      this.#point
    );
  };

  #handleDeleteButton = () => {
    this.#handleDataUpdate(
      UserAction.DELETE_POINT,
      UpdateType.MAJOR,
      this.#point
    );
  };
}
