import TripPresenter from './presenter/trip-presenter';
import PointsModel from './model/points-model';
import FilterPresenter from './presenter/filter-presenter';
import FilterModel from './model/filter-model';

const tripPointsContainer = document.querySelector('.trip-events'); //        
const filterContainer = document.querySelector('.trip-controls__filters'); //    Filter

const pointsModel = new PointsModel();
const filterModel = new FilterModel();

const tripPresenter = new TripPresenter(tripPointsContainer, pointsModel); //    ,     
const filterPresenter = new FilterPresenter(filterContainer, filterModel);

tripPresenter.init();
filterPresenter.init();
