import TripSort from '../view/sort-view';
import TripFilter from '../view/trip-filter-view';
import NoPointView from '../view/no-point-view';
import NewPointView from '../view/new-point-view';
import PointPresenter from './point-presenter';
import TripPointListView from '../view/event-list-view';
import { defaultNewPoint } from '../mock/points';
import { render, RenderPosition, remove } from '../framework/render';
import { SortType, UserAction, UpdateType } from '../const';
import { sortingByPrice, sortingByDay, sortingByTime } from '../utils';

/**
 * @class       UI- (, , ).
 */
export default class TripPresenter {
  #tripContainer = null; //    (  main.js)
  #pointsModel = null; //   (  main.js)

  #tripMainContainer = document.querySelector('.trip-main'); //  Header  ,  , 
  #tripFiltersContainer = document.querySelector('.trip-controls__filters'); //    Filter
  #tripSortComponent = null; //  ()
  #tripListComponent = null; //  ul    li( )
  #tripFilterComponent = null; //  Filter,   this.#tripFiltersContainer
  #pointPresenters = new Map(); // MAP     Point
  #currentSortType = SortType.DAY; //     (   DAY )

  /**
   * @constructor
   * @param  tripContainer     ( `.trip-events`)
   * @param  pointsModel    ,   
   */
  constructor(tripContainer, pointsModel) {
    this.#tripContainer = tripContainer;
    this.#pointsModel = pointsModel;

    this.#pointsModel.addObserver(this.#handleModeChange); //     
  }

  /**
   *          
   */
  get points() {
    switch (this.#currentSortType) {
      case SortType.TIME:
        return this.#pointsModel.points.sort(sortingByTime); //    
      case SortType.PRICE:
        return this.#pointsModel.points.sort(sortingByPrice);
    }
    return this.#pointsModel.points.sort(sortingByDay);
  }

  /**
  *        
  */
  get destinations() {
    return this.#pointsModel.destinations;
  }

  /**
  *      .
  */
  get offers() {
    return this.#pointsModel.offers;
  }

  /**
   *   TripPresenter
   */
  init() {
    this.#renderHeader();
    this.#renderSortComponent();
    this.#renderPoints();
  }

  /**
   *    
   */
  #renderHeader() {
    this.#renderFilterComponent();
    render(new NewPointView({
      onAddButtonClick: this.#handleNewPointButton,
    }), this.#tripMainContainer, RenderPosition.AFTEREND);
  }

  /**
   *        (Points)
   */
  #renderFilterComponent() {
    this.#tripFilterComponent = new TripFilter();
    render(this.#tripFilterComponent, this.#tripFiltersContainer);
  }

  /**
   *     ,     
   */
  #renderEmptyPage() {
    if (this.#tripSortComponent !== null) {
      remove(this.#tripSortComponent);
    }
    render(new NoPointView(), this.#tripContainer);
    //         
    const filterInputs = document.querySelectorAll('.trip-filters__filter-input');
    filterInputs.forEach((input) => {
      input.disabled = true;
    });
  }

  /**
   *     
   * @param onSortTypeChange -     
   */
  #renderSortComponent() {
    //   (   - )
    this.#tripSortComponent = new TripSort({
      onSortTypeChange: this.#handleSortTypeChange
    });

    render(this.#tripSortComponent, this.#tripContainer);
  }

  /**
   *       
   */
  #renderPoints() {
    if (this.points.length !== 0) {
      if (this.#tripListComponent === null) {
        this.#tripListComponent = new TripPointListView();
        render(this.#tripListComponent, this.#tripContainer); render(this.#tripListComponent, this.#tripContainer);
      }
      for (let i = 0; i < this.points.length; i++) {
        const pointPresenter = new PointPresenter({
          pointListContainer: this.#tripListComponent.element,
          point: this.points[i],
          destinations: this.destinations,
          offers: this.offers,
          handleEditTypeChange: this.#handleCloseAllForm,
          handleDataChange: this.#handleViewAction,
        });

        pointPresenter.init();
        this.#pointPresenters.set(this.points[i].id, pointPresenter); //    Map
      }
    } else {
      this.#renderEmptyPage();
    }
  }

  #handleNewPointButton = () => {
    console.log('   " "');
    const pointPresenter = new PointPresenter({
      point: defaultNewPoint,
      destinations: this.destinations,
      offers: this.offers,
      handleEditTypeChange: this.#handleCloseAllForm,
      handleDataChange: this.#handleViewAction,
    });
    console.log(pointPresenter);
    pointPresenter.init();
  };

  /**
  *     
  * @param sortType -  ,  
  */
  #handleSortTypeChange = (sortType) => {
    if (this.#currentSortType === sortType) {
      return;
    }
    this.#currentSortType = sortType; //       
    this.#clearBoard();
    this.#renderPoints();
  };

  /**
   *  -     
   */
  #handleCloseAllForm = () => {
    this.#pointPresenters.forEach((presenter) => presenter.resetViewToDefault());
  };

  #clearBoard = () => {
    this.#pointPresenters.forEach((presenter) => presenter.destroy());
  };

  /**
 * -,     
 * @param updateType -  
 * @param data -    
 */
  #handleModeChange = (updateType, data) => {
    switch (updateType) {
      case UpdateType.MAJOR: //    Board
        this.#clearBoard();
        this.#renderPoints();
        break;
      case UpdateType.MINOR: //  
        this.#pointPresenters.get(data.id)._replaceComponent(data);
        break;
      case UpdateType.PATCH: //   .   ?
        break;
    }
  };

  /**
   * @description -,     
   * @param {*} actionType -  
   * @param {*} updateType -  
   * @param {*} update -    
   */
  #handleViewAction = (actionType, updateType, update) => {
    switch (actionType) {
      case UserAction.ADD_POINT:
        this.#pointsModel.addPoint(updateType, update);
        break;
      case UserAction.DELETE_POINT:
        this.#pointsModel.deletePoint(updateType, update);
        break;
      case UserAction.UPDATE_POINT:
        this.#pointsModel.updatePoints(updateType, update);
        break;
    }
  };
}
